  showNotification(id, from: any, align: any, color:any ,content:any) {
    console.log('dddddddddddddddddddddddddddd')
    var temp_client = JSON.parse(localStorage.getItem('current_client'))
     
    if(temp_client){
      this.auth.getFavouritesDetail(temp_client['_id'], id).subscribe((res)=>{
        console.log(res)
        if(res == 'Existed Favourite')
            this.auth.showNotification(from,align, 4, 'This vendor has already registed Favourite');
        else
        this.auth.showNotification(from,align, 2, 'This vendor has just registed Favourte');
        this.auth.getFavourites(temp_client['_id']).subscribe((res)=>{
          var tempArr = res;
          tempArr.forEach(element => {
            this.favouriteArr.push(element.vendorId._id)
          });
        });

      })
    }else{
      this.auth.showNotification(from,align, 4, 'You have to login first');
    }
  }
  onItemChange(txt,value){
    // alert(value)
    switch(txt){
      case 'Value':
        this.review_Value = 6 - Number(value);
        break;
      case 'Flexibility':
        this.review_Flexibility = 6 - Number(value);
        break;
      case 'Professionalism':
        this.review_Professionalism = 6 - Number(value);
          break;
      case 'Responsiveness':
        this.review_responsiveness = 6 - Number(value);
          break;
      case 'Behaviour':
        this.review_Behaviour = 6 - Number(value);
          break;
    };
    this.onCommentChange();
    console.log(" Value is : ", value );
 }
  setConformFlag(val){
      this.confirmFlag += val;
      if(val == -1){
        this.registerContactForm = this.formBuilder.group({
          fullname:this.contact_full_name,
          email: this.contact_email,
          hash:this.contact_hash,
          phone:this.contact_phone
        }); 
        this.auth.signUp(this.registerContactForm.value).subscribe((res) => {
          console.log('res' + res);
          this.resendPhone();
          this.auth.showSwal('success-message','Success', 'You registered successfully and have to send verification code!');
         
          this.auth.signIn(this.registerContactForm.value).subscribe((res: any) => {
            localStorage.setItem('access_token', res.token)
            // this.emit('client name added',res._id);
            console.log(res._id);
            this.auth.getClientProfile(res._id).subscribe((res) => {
              this.auth.current_Client = res;
              localStorage.setItem('_id', res._Id)
              localStorage.setItem('current_client',JSON.stringify(this.auth.current_Client));
              // this.resetConfirm();
              // location.assign('/');
            });
          })

          // this.registerForm.reset()
          // this.auth.showSwal('success-message','Welcome', 'you have been registered successfully');
        },(err)=>{
          this.auth.showSwal('warning-message','Wrong', 'Already Exist Email Address!');

        })
      }
  }
  setQuotationFlag(val){
    this.quotationFlag += val;
    if(val == -1){
      this.registerQuotationForm = this.formBuilder.group({
        fullname:this.get_full_name,
        email: this.get_email,
        hash:this.quotation_hash,
        phone:this.get_phone
      }); 
      this.auth.signUp(this.registerQuotationForm.value).subscribe((res) => {
        console.log('res' + res);
        this.resendQuotationPhone();
        this.auth.showSwal('success-message','Success', 'You registered successfully and have to send verification code!');
       
        this.auth.signIn(this.registerQuotationForm.value).subscribe((res: any) => {
          localStorage.setItem('access_token', res.token)
          // this.emit('client name added',res._id);
          console.log(res._id);
          this.auth.getClientProfile(res._id).subscribe((res) => {
            this.auth.current_Client = res;
            localStorage.setItem('_id', res._Id)
            localStorage.setItem('current_client',JSON.stringify(this.auth.current_Client));
            // location.assign('/');
          });
        })

        // this.registerForm.reset()
        // this.auth.showSwal('success-message','Welcome', 'you have been registered successfully');
      },(err)=>{
        this.auth.showSwal('warning-message','Wrong', 'Already Exist Email Address!');

      })
    }
}
