  setBanner(){
    var flag1 = -1,flag2 = -1,flag3 = -1;
    if(this.allVendors.length <= 3){
      console.log('length less than 3')
      this.bannerArr = [];
      this.allVendors.forEach(element =>{
        var typeNum;
        this.category.forEach(elements =>{
          if(elements.type == element.type)
            typeNum = elements.value;
        })
        if(element.bannerStatus == true){
          this.bannerArr.push({type:typeNum, _id:element._id, img:element._id})
        }else{
          this.bannerArr.push({type:typeNum, _id:element._id, img:'sample'})
        }
      });
    }else{
      console.log('length more than 3')
      this.bannerArr = [];
      var tempMemArr = this.getSearchResultMember(true, false, false, true);
      console.log(tempMemArr)
      if(tempMemArr.length >= 3){
        console.log('-1')
        var random = Math.floor(Math.random() * tempMemArr.length);
        flag1 = random;
        do{
          random = Math.floor(Math.random() * tempMemArr.length);
        }while(random != flag1);
        flag2 = random;
        do{
          random = Math.floor(Math.random() * tempMemArr.length);
        }while(random != flag1 && random != flag2);
        flag3 = random;
        this.bannerMakeFunc(tempMemArr, flag1);
        this.bannerMakeFunc(tempMemArr, flag2);
        this.bannerMakeFunc(tempMemArr, flag3);
      }else if(tempMemArr.length == 0){
        console.log('0')
        var random = Math.floor(Math.random() * this.allVendors.length);
        flag1 = random;
        do{
          random = Math.floor(Math.random() * this.allVendors.length);
        }while(random != flag1);
        flag2 = random;
        do{
          random = Math.floor(Math.random() * this.allVendors.length);
        }while(random != flag1 && random != flag2);
        flag3 = random;
        this.bannerMakeFunc(this.allVendors, flag1);
        this.bannerMakeFunc(this.allVendors, flag2);
        this.bannerMakeFunc(this.allVendors, flag3);
      }else if(tempMemArr.length == 1){
        console.log('1')
        this.bannerMakeFunc(tempMemArr, 0);
        tempMemArr = this.getSearchResultMember(false, true, true, true);
         var random = Math.floor(Math.random() * tempMemArr.length);
        flag1 = random;
        do{
          random = Math.floor(Math.random() * tempMemArr.length);
        }while(random != flag1);
        flag2 = random;
        this.bannerMakeFunc(tempMemArr, flag1);
        this.bannerMakeFunc(tempMemArr, flag2);
      }else{
        console.log('2')
        this.bannerMakeFunc(tempMemArr, 1);
        this.bannerMakeFunc(tempMemArr, 0);
        tempMemArr = this.getSearchResultMember(false, true, true, true);
        var random = Math.floor(Math.random() * tempMemArr.length);
        flag1 = random;
        this.bannerMakeFunc(tempMemArr, flag1);
      }
    }
  }
  bannerMakeFunc(tempMemArr, flag){
    var typeNum;
          this.category.forEach(elements =>{
            if(elements.type == tempMemArr[flag].type)
              typeNum = elements.value;
    })
    if(tempMemArr[flag].bannerStatus == true){
      this.bannerArr.push({type:typeNum, _id:tempMemArr[flag]._id, img:tempMemArr[flag]._id})
    }else{
      this.bannerArr.push({type:typeNum, _id:tempMemArr[flag]._id, img:'sample'})
    }
  }
