import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { AuthWedService } from './services/auth.service';
import { SearchItem } from './models/searchitem';

@Component({
    selector: 'app-my-app',
    templateUrl: './app.component.html'
})

export class AppComponent implements OnInit {
  private _router: Subscription;

  constructor( private router: Router, private aws:AuthWedService ) {
    this.aws.getCities().subscribe((res)=>{
      localStorage.setItem('cities', JSON.stringify(res));
      this.aws.cities = res;
      console.log('aws')
      console.log(this.aws.cities)
    })
    this.aws.getLocations().subscribe((res)=>{
      localStorage.setItem('areas', JSON.stringify(res));
      this.aws.areas = res;
      console.log(this.aws.areas)
    })
    this.aws.getAllBlogCategories().subscribe((res)=>{
      localStorage.setItem('blogcategories', JSON.stringify(res));
      this.aws.blogCategories = res;
    })
    this.aws.getSearchAll().subscribe((res)=>{
      console.log('getsearchall')
      console.log(res)
      localStorage.setItem('allvendors', JSON.stringify(res));
      this.aws.allVendors = res;
      console.log('aws')
      console.log(this.aws.allVendors)
    })
    this.aws.getBlogs().subscribe((res)=>{
      this.aws.allBlog = res;
      localStorage.setItem("allBlogs", JSON.stringify(res));
    })
    if(localStorage.getItem('search_item') != null)
        this.aws.search_items = JSON.parse(localStorage.getItem('search_item'));
    else{
        this.aws.search_items = new SearchItem;
      this.aws.setSearchItem();
    }
  
  }

    ngOnInit() {
      this._router = this.router.events.filter(event => event instanceof NavigationEnd).subscribe((event: NavigationEnd) => {
        const body = document.getElementsByTagName('body')[0];
        const modalBackdrop = document.getElementsByClassName('modal-backdrop')[0];
        if (body.classList.contains('modal-open')) {
          body.classList.remove('modal-open');
          modalBackdrop.remove();
        }
      });
      // this.aws.getSearch();
      
    }
}
