      this.regularItems[1] = 'City: ' + tempArr['businessCityName'];
      var tempClient = JSON.parse(localStorage.getItem('current_client'))
    if(tempClient)
      this.auth.getFavourites(tempClient['_id']).subscribe((res)=>{
        var tempArr = res;
        tempArr.forEach(element => {
          this.favouriteArr.push(element.vendorId._id)
        });
      });
