  regularChange(aaa){
    // var temp = JSON.parse(localStorage.getItem('search_item'));
    const txtService = ['Service : HaveStudio','Service : TravelToClientVenue','Service : RoomsAvaliable','Service : TravelOtherIndianCities','Service : ProvideBackupDancers' ];
    var flag_city = false, flag_type = false, flag_price = false;
    var flag_service = [false,false,false,false,false]; 
      aaa.forEach(element=> {
      console.log(element)
        if (element.includes('City:')){
          flag_city = true;
        }
        if (element.includes('Type:')){
          flag_type = true;
        }
        if (element.includes('Price :')){
          flag_price = true;
        }
        if(element.includes(txtService[0]))
          flag_service[0] = true; 
        if(element.includes(txtService[1]))
          flag_service[1] = true;
        if(element.includes(txtService[2]))
          flag_service[2] = true;
        if(element.includes(txtService[3]))
          flag_service[3] = true;
        if(element.includes(txtService[4]))
          flag_service[4] = true;
      })
      for(let i = 0;i <5;i ++){
        if(flag_service[i] == false){
          switch(i){
            case 0:
              if(this.isObj['haveStudio']){
                this.isObj['haveStudio'] = false;
                this.setHandleCheck(0, false)
              }
              break;
              case 1:
              if(this.isObj['travelToClientVenue']){
                this.isObj['travelToClientVenue'] = false;
                this.setHandleCheck(1, false)
              }
              break;
              case 2:
              if(this.isObj['roomsAvaliable']){
                this.isObj['roomsAvaliable'] = false;
                this.setHandleCheck(2, false)
              }
              break;
              case 3:
              if(this.isObj['travelOtherIndianCities']){
                this.isObj['travelOtherIndianCities'] = false;
                this.setHandleCheck(3, false)
              }
              break;
              case 4:
                if(this.isObj['provideBackupDancers']){
                  this.isObj['provideBackupDancers'] = false;
                  this.handleClick(4, true)
                }
                break;
          }
        }
      }
      if(flag_price == false){
        this.radioPriceControl = new FormControl('');
        // this.searchResult = this.searchResultTemp
        this.auth.search_items.amount = undefined;
        this.auth.setSearchItem();
    }
      if(flag_city == false){
          this.auth.search_items.businessCity = undefined;
          this.auth.setSearchItem();
            // delete temp.businessCity;
        // delete temp.businessCityName;
      }if(flag_type == false){
        this.auth.search_items.type = undefined;
        this.auth.setSearchItem();
        // delete temp.type;
      }

      // localStorage.setItem('search_item', JSON.stringify(temp));
      this.searchResult = this.auth.getSearchResultArr();
  }
