    if(this.curType == 1){
      this.auth.search_items.budget = this.isBudgetPhoto;
      this.auth.search_items.services = []; 
      this.servicesPhotoArr.forEach(element => {
      if(this.isServicesPhoto[element.value])
        this.auth.search_items.services.push(element.type) 
      });
    }
    if(this.curType == 0 || this.curType == 5)
      this.auth.search_items.budget = this.isBudgetVenue;
