import { Routes } from '@angular/router';

import { ClientLayoutComponent } from './layouts/client/client-layout.component';
import { HomeComponent } from './client/home/home.component';
import { ClientlistComponent } from './client/clientlist/clientlist.component';
import { ClientvendorsearchComponent } from './client/clientvendorsearch/clientvendorsearch.component';
import { ClientvendordetailComponent } from './client/clientvendordetail/clientvendordetail.component';
import { AdminLayoutComponent } from './layouts/admin/admin-layout.component';
import { BlogLayoutComponent } from './layouts/blog/blog-layout.component';

import { BlogComponent } from './blog/blog/blog.component';
import { BlogsearchComponent } from './blog/blog/blogsearch/blogsearch.component';
import { BlogdetailComponent } from './blog/blog/blogdetail/blogdetail.component';
import { PolicyComponent } from './policy/policy.component';
import { OwnerLayoutComponent } from './layouts/owner/owner-layout.component';

export const AppRoutes: Routes = [
    {
        path:'',
        component:ClientLayoutComponent,
        children:[
            {
                path:'',
                component:HomeComponent
            },
            {
                path:'list',
                component:ClientlistComponent
            },
            {
                path:'myprofile',
                loadChildren:'./client/clientprofile/clientprofile.module#ClientprofileModule'
            },
            {
                path:'vendor',
                component:ClientvendorsearchComponent
            },
            {
                path:'vendor/:id',
                component:ClientvendorsearchComponent
            },
            {
                path:'vendor/:id/:detail',
                component:ClientvendordetailComponent
            },{
                path:'privacy-policy',
                component:PolicyComponent
            }
        ]
    },{
        path:'admin',
        component:AdminLayoutComponent,
        children:[
            {
                path: '',
                loadChildren: './vendor/vendor.module#VendorModule'
            },
        ]
    },{
        path:'owner',
        component:OwnerLayoutComponent,
        children:[
            {
                path: '',
                loadChildren: './owner/owner.module#OwnerModule'
            },
        ]
    },
    {
        path:'blog',
        component:BlogLayoutComponent,
        children:[
            {
                path:'',
                component:BlogComponent
            },
            {
                path:'search',
                component:BlogsearchComponent
            },
            {
                path:'search/:id',
                component:BlogsearchComponent
            },
            {
                path:'search/:id/:detail',
                component:BlogdetailComponent
            }
        ]
    }
];
