import { Component, OnInit, ElementRef, OnDestroy } from '@angular/core';
// import csc from 'country-state-city';
import { AuthWedService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import { FormBuilder,FormGroupDirective,NgForm, FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit ,OnDestroy{
  test: Date = new Date();
  private toggleButton: any;
  private sidebarVisible: boolean;
  private nativeElement: Node;
  simpleSlider = 40;
  doubleSlider = [20, 60];
  categories;
  cities:any;
  regularItems = [];
  touch: boolean;

  selectedValue: string;
  currentCity: string[];
  blogList;
  typeControl = new FormControl('',[Validators.required]);
  cityControl = new FormControl('',[Validators.required]);
  findForm: FormGroup;

  selectTheme = 'primary';

  constructor(private router:Router,private element: ElementRef, private auth:AuthWedService, private formBuilder: FormBuilder) {
    this.nativeElement = element.nativeElement;
      this.sidebarVisible = false;
      console.log('category')
      this.categories = this.auth.category;
      console.log(this.categories)
      // this.cities = csc.getStatesOfCountry('101');
      this.cities = JSON.parse(localStorage.getItem('cities'))
      this.findForm = this.formBuilder.group({
        type: this.typeControl,
        businessCity:this.cityControl,
      }); 
      this.blogList  = [];
      this.auth.getBlogs().subscribe((res)=>{
        console.log('>>>>>>>>>>>    Blogs   <<<<<<<<<<<<<<<<<')
        console.log(res)
        this.blogList = res;
        console.log('>>>>>>>>>>>    End Blogs   <<<<<<<<<<<<<<<<<')
    })
  }
  
  find(){
    // localStorage.setItem('search_item', JSON.stringify({
    //   'type': this.auth.category[this.typeControl.value].type,
    //   'businessCity':this.cityControl.value,
    // }));
    this.auth.search_items.type = this.auth.category[this.typeControl.value].type;
    this.auth.search_items.businessCity = this.cityControl.value;
    
    this.auth.search_items.businessCityName = this.cities[this.cityControl.value].name;
    alert(this.cityControl.value)
    this.auth.setSearchItem();
    // this.auth.getSearch().subscribe((res)=>{
      // console.log('getsearch')
      // this.auth.search_Vendor = JSON.parse(localStorage.getItem('search_item'))
      // console.log(this.auth.search_Vendor)
    // });
    this.router.navigate(['vendor/' + this.typeControl.value]);
  }
  ngOnInit() {
      var navbar : HTMLElement = this.element.nativeElement;
      this.toggleButton = navbar.getElementsByClassName('navbar-toggle')[0];
      const body = document.getElementsByTagName('body')[0];
      body.classList.add('login-page');
      body.classList.add('off-canvas-sidebar');
      const card = document.getElementsByClassName('card')[0];
      setTimeout(function() {
          // after 1000 ms we add the class animated to the login/register card
          card.classList.remove('card-hidden');
      }, 700);
      this.router.events.subscribe((event:any) => {
        window.scrollTo(0, 0);
      });
  }
  sidebarToggle() {
      var toggleButton = this.toggleButton;
      var body = document.getElementsByTagName('body')[0];
      var sidebar = document.getElementsByClassName('navbar-collapse')[0];
      if (this.sidebarVisible == false) {
          setTimeout(function() {
              toggleButton.classList.add('toggled');
          }, 500);
          body.classList.add('nav-open');
          this.sidebarVisible = true;
      } else {
          this.toggleButton.classList.remove('toggled');
          this.sidebarVisible = false;
          body.classList.remove('nav-open');
      }
  }
  ngOnDestroy(){
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('login-page');
    body.classList.remove('off-canvas-sidebar');
  }
  selCity(sCity){
    // localStorage.setItem('search_item', JSON.stringify({
    //   'businessCity':sCity,
    //   'businessCityName':this.cities[sCity].name,
    // }));
    this.auth.search_items.type = 'Venue'; 
    this.auth.search_items.businessCity = sCity;
    this.auth.search_items.businessCityName = this.cities[sCity].name;
    this.auth.setSearchItem();
    this.router.navigate(['vendor/' + 0]);
  }
}
