import { Component, OnInit, Output, EventEmitter ,OnDestroy } from '@angular/core';
import { FormBuilder,FormGroupDirective,NgForm, FormControl, FormGroup, Validators } from '@angular/forms';
import {AuthService, GoogleLoginProvider} from 'angular4-social-login';
import { Observable, throwError, Subscriber, Subject } from 'rxjs';
import { AuthWedService } from '../services/auth.service';
import { ElementRef } from '@angular/core';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
@Component({
  selector: 'app-ownerlogin',
  templateUrl: './ownerlogin.component.html',
  styleUrls: ['./ownerlogin.component.css']
})
export class OwnerloginComponent implements OnInit {
  private toggleButton: any;
  private sidebarVisible: boolean;
  private nativeElement: Node;
  @Output() ownerFlag = new EventEmitter<any>();

  loginForm: FormGroup;
  type:number = 0;
  user = new FormControl('',[
   Validators.required
 ]);
 hash = new FormControl('',[
   Validators.required
 ]);

 constructor(    private auth: AuthWedService,
  private formBuilder: FormBuilder,
  private _socioAuthServ:AuthService,
  private router: Router, private element: ElementRef) { 
    this.nativeElement = element.nativeElement;
    this.sidebarVisible = false;
  }
  ngOnInit() {
    var navbar : HTMLElement = this.element.nativeElement;
    this.toggleButton = navbar.getElementsByClassName('navbar-toggle')[0];
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('login-page');
    body.classList.add('off-canvas-sidebar');
    body.style.padding = '0';
    const card = document.getElementsByClassName('card')[0];
    setTimeout(function() {
        // after 1000 ms we add the class animated to the login/register card
         card.classList.add('card-hidden');
        card.classList.remove('card-hidden');
    }, 700);
    this.loginForm = this.formBuilder.group({
      user: this.user,
      hash:this.hash,
    }); 
}
sidebarToggle() {
    var toggleButton = this.toggleButton;
    var body = document.getElementsByTagName('body')[0];
    var sidebar = document.getElementsByClassName('navbar-collapse')[0];
    if (this.sidebarVisible == false) {
        setTimeout(function() {
            toggleButton.classList.add('toggled');
        }, 500);
        body.classList.add('nav-open');
        this.sidebarVisible = true;
    } else {
        this.toggleButton.classList.remove('toggled');
        this.sidebarVisible = false;
        body.classList.remove('nav-open');
    }
}
ngOnDestroy(){
  const body = document.getElementsByTagName('body')[0];
  body.classList.remove('login-page');
  body.classList.remove('off-canvas-sidebar');
}
login() {
  this.auth.signInOwner(this.user.value, this.hash.value).subscribe((res: any) => {
    localStorage.setItem('access_token_owner', res.token)
      localStorage.setItem('owner','true');
      this.auth.showSwal('success-message','Welcome!', 'you have been logged in successfully.');
      this.ownerFlag.emit(true);

    },
  (err:any)=>{
    this.auth.showSwal('warning-message','Failed!', 'you must input correct info!!!');

  })
  } 
   
}
