import { Component, OnInit, Output,EventEmitter } from '@angular/core';
import { FormBuilder,FormGroupDirective,NgForm, FormControl, FormGroup, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { AuthWedService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  @Output() logFlag = new EventEmitter<any>(); 
  registerForm: FormGroup;
  isPhone:boolean = false;
  fullname = new FormControl('',[
    Validators.required,
    Validators.minLength(3)
  ]);
  email = new FormControl('',[
    Validators.required,
    Validators.minLength(3),
    Validators.maxLength(100),
    Validators.pattern(EMAIL_REGEX)
  ]);
  hash = new FormControl('',[
    Validators.required,
    Validators.minLength(6)
  ]);
  phone = new FormControl('',[
    Validators.required,
    Validators.minLength(10),
    Validators.maxLength(10),
  ])
  randPhone:String;

  verifyCode = new FormControl('',[
    Validators.required
  ])
  constructor(    private auth: AuthWedService,
    private formBuilder: FormBuilder,
    private router: Router) { 
  }

  ngOnInit() {
    this.registerForm = this.formBuilder.group({
      fullname:this.fullname,
      email: this.email,
      hash:this.hash,
      phone:this.phone
    }); 
  }
  register() {
    console.log(this.registerForm.value)
      this.auth.signUp(this.registerForm.value).subscribe((res) => {
        console.log('res' + res);
        this.isPhone = true;
        this.resendPhone();
        // this.registerForm.reset()
        // this.auth.showSwal('success-message','Welcome', 'you have been registered successfully');
      })
  } 
  onLogin(){
    this.logFlag.emit({type:'register',flag:true});
  }
  onResend(){
    this.sendPhone();
  }
  sendPhone(){
    if(this.verifyCode.value == this.randPhone){
        this.auth.showSwal('success-message','Success', 'you have just verified phone');
        // this.auth.showNotification()
      this.auth.setVerifyClientPhoneEmail(this.email.value).subscribe((res)=>{
        this.logFlag.emit({type:'register',flag:true});
        console.log(res)
          // this.regFlag.emit(true);
      })
    }else{
        this.auth.showSwal('warning-message', 'Wrong', 'you must input correct info!!!')
    }

  }
  skipPhone(){
    this.logFlag.emit({type:'register',flag:true});
  }
  resendPhone(){
    this.randPhone = Math.floor(Math.random() * 10).toString() + Math.floor(Math.random() * 10) + Math.floor(Math.random() * 10) +Math.floor(Math.random() * 10);
    //  this.auth.showSwal('success-message','Welcome', 'we have just sent message');
      this.auth.phoneClientSendMsg(this.randPhone,this.phone.value).subscribe((res)=>{
      console.log('======== resendphone res ======')
      console.log(res)
    },(err)=>{
      console.log('======== resendphone err======')
      console.log(err)
    })
  }
}
