@Component({
    selector: 'app-sidebar-cmp',
    templateUrl: 'sidebar.component.html',
})

export class SidebarComponent implements OnInit {
    public menuItems: any[];
    ps: any;
    isMobileMenu() {
        if ($(window).width() > 991) {
            return false;
        }
        return true;
    };
    constructor(private auth:AuthWedService,private router:Router){
        var tempRoute = ROUTES;
        if(localStorage.getItem('current_vendor')){
            var tempObj = JSON.parse(localStorage.getItem('current_vendor'))
            console.log('>>>>')
        console.log('==========================================================================')
        console.log(tempObj)
        console.log('==========================================================================')
        if(this.auth.current_Vendor && this.auth.current_Vendor['basicStatus']){
                 tempRoute[0].flag = this.auth.current_Vendor['basicStatus']['basicDetailsStatus']
        tempRoute[1].flag = this.auth.current_Vendor['basicStatus']['contactInfoStatus']
        tempRoute[2].flag = this.auth.current_Vendor['basicStatus']['servicePricingStatus']   
        }else{
            tempRoute[0].flag = tempObj['basicStatus']['basicDetailsStatus']
            tempRoute[1].flag = tempObj['basicStatus']['contactInfoStatus']
            tempRoute[2].flag = tempObj['basicStatus']['servicePricingStatus']   
        }

            if(tempObj['status'] == 'allow'){
                if(localStorage.getItem('vendor_sidebar_flag') == 'true')
                    this.menuItems = tempRoute.filter(menuItem => menuItem);
                else
                this.menuItems = ROUTES1.filter(menuItem => menuItem);
            }
            else
                this.menuItems = tempRoute.filter(menuItem => menuItem);

        }else{
            this.menuItems = tempRoute.filter(menuItem => menuItem);
      }
    }
    ngOnInit() {
        var tempRoute = ROUTES;
        // if(this.auth.get)
            // localStorage.setItem('vendor_sidebar_flag','false')
            this.router.events.subscribe((res)=>{
                console.log('>><<>><< SIDE abr <><><><>>')

                    console.log(res)
                 console.log('>><<>><< SIDE abr <><><><>>')
                if(localStorage.getItem('current_vendor')){
                      var tempObj = JSON.parse(localStorage.getItem('current_vendor'))
                      tempRoute[0].flag = tempObj['basicStatus']['basicDetailsStatus']
                    tempRoute[1].flag = tempObj['basicStatus']['contactInfoStatus']
                    tempRoute[2].flag = tempObj['basicStatus']['servicePricingStatus']   
                    if(tempObj['status'] == 'allow'){
                        if(localStorage.getItem('vendor_sidebar_flag') == 'true')
                            this.menuItems = tempRoute.filter(menuItem => menuItem);
                        else
                        this.menuItems = ROUTES1.filter(menuItem => menuItem);
                    }
                    else
                        this.menuItems = tempRoute.filter(menuItem => menuItem);
        
                }else{
                    if(this.auth.current_Vendor && this.auth.current_Vendor['basicStatus']){
                        tempRoute[0].flag = this.auth.current_Vendor['basicStatus']['basicDetailsStatus']
                        tempRoute[1].flag = this.auth.current_Vendor['basicStatus']['contactInfoStatus']
                        tempRoute[2].flag = this.auth.current_Vendor['basicStatus']['servicePricingStatus']   
                        }
                    this.menuItems = tempRoute.filter(menuItem => menuItem);
                }   
        })
     
        if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
            const elemSidebar = <HTMLElement>document.querySelector('.sidebar .sidebar-wrapper');
            this.ps = new PerfectScrollbar(elemSidebar);
        }
    }
    updatePS(): void  {
        if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
            this.ps.update();
        }
    }
    isMac(): boolean {
        let bool = false;
        if (navigator.platform.toUpperCase().indexOf('MAC') >= 0 || navigator.platform.toUpperCase().indexOf('IPAD') >= 0) {
            bool = true;
        }
        return bool;
    }
}
