import { Component, OnInit } from '@angular/core';
import { AuthWedService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import { FormBuilder,FormGroupDirective,NgForm, FormControl, FormGroup, Validators } from '@angular/forms';

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {
  categories;
  cities:any;
  addEmailForm: FormGroup;
  email = new FormControl('',[
    Validators.required,
    Validators.minLength(3),
    Validators.maxLength(100),
    Validators.pattern(EMAIL_REGEX)
  ]);
  constructor( private auth:AuthWedService, private route:Router, private formBuilder: FormBuilder,) {
    this.categories = this.auth.category;
    this.auth.getCities().subscribe((res)=>{
      this.cities = res;
    })
  }

  ngOnInit() {
    this.categories = this.auth.category;
    this.addEmailForm = this.formBuilder.group({
      email: this.email,
    }); 
   }
  selCityType(tVal, cVal){
    localStorage.setItem('search_item', JSON.stringify({
      'type':this.auth.category[tVal].type,
      'businessCity':cVal,
      'businessCityName':this.cities[cVal].name,
    }));
    this.route.navigate(['/vendor',tVal]);
  }
  addEmail(){
    this.auth.addEmail(this.email.value).subscribe((res: any) => {
        console.log(res);
        this.auth.showNotification('top','right',2, 'This Email has just submitted correctly !')
    },(err:any)=>{
      this.auth.showNotification('top','right',4, 'Some Errors Occured')
    })
    console.log('login');
  }
}
