import { Component, OnInit } from '@angular/core';
import { AuthWedService } from 'src/app/services/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder,FormGroupDirective,NgForm, FormControl, FormGroup, Validators } from '@angular/forms';

// import { faFacebookSquare } from '@fortawesome/free-brands-svg-icons/faFacebookSquare';
// import { faTwitterSquare } from '@fortawesome/free-brands-svg-icons/faTwitterSquare';
// import { faPinterest } from '@fortawesome/free-brands-svg-icons/faPinterest';
@Component({
  selector: 'app-blogdetail',
  templateUrl: './blogdetail.component.html',
  styleUrls: ['./blogdetail.component.css']
})
export class BlogdetailComponent implements OnInit {
  // fbIcon = faFacebookSquare;
  // pinIcon = faPinterest;
  // tweetIcon = faTwitterSquare;

  detailInfo = {};
  blogTags:Array<Object>= [];
  categoryControl =  new FormControl('',[Validators.required]);
  cityControl =  new FormControl('',[Validators.required]);
  searchForm: FormGroup;
  categories;
  vendor_category_arr = [];
  blogCategories;
  subParts = [];
  cities:any;
  category_new;
  similarBlogs:any;
  bannerArr = [];
  carouselArr = [];
  categorySel;
  constructor(public auth:AuthWedService, private router:Router, private route:ActivatedRoute,private formBuilder: FormBuilder) {
    if( this.auth.bannerArr.length ==0 )
        this.auth.setBanner();
    // this.blogCategories = auth.blog_category;
    this.vendor_category_arr = this.auth.category;
    this.blogCategories = auth.blogCategories;
    this.categories = this.auth.category;
    this.cities= this.auth.cities;
    this.category_new = this.auth.category_new;
    this.auth.blogCategories.forEach(element =>{
      if(element.value == this.route.snapshot.params.id)
          {this.categorySel = element;
            this.auth.getBlogsCategory(this.categorySel._id).subscribe((res)=>{
              this.similarBlogs = res;
          })
          }
    })
  
    this.searchForm = this.formBuilder.group({
      type: this.categoryControl,
      businessCity:this.cityControl,
    }); 
      this.bannerArr = this.auth.bannerArr;
      this.carouselArr = this.auth.allVendors;
   }

  ngOnInit() {
    this.auth.getBlogDetail(this.route.snapshot.params.detail).subscribe((res)=>{
      this.detailInfo = res;
      this.blogTags = this.detailInfo['blogTag']
      console.log('blogTags')
      console.log(this.blogTags)
      console.log('blogTags')
      if(this.detailInfo['subParts'])
      this.subParts = this.detailInfo['subParts'];
      console.log('detailinfo')
      console.log(res)
    })

  }
  lookingFor(){
    localStorage.setItem('search_item', JSON.stringify({
      'type': this.auth.category[this.categoryControl.value].type,
      'businessCity':this.cityControl.value,
    }));
    this.auth.getSearch().subscribe((res)=>{
      // console.log('getsearch')
      // this.auth.search_Vendor = JSON.parse(localStorage.getItem('search_item'))
      // console.log(this.auth.search_Vendor)
      
      this.router.navigate(['vendor/' + this.categoryControl.value]);
    });
  }
}
