import { Component, OnInit, Output,EventEmitter } from '@angular/core';
import { FormBuilder,FormGroupDirective,NgForm, FormControl, FormGroup, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { AuthWedService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import {AuthService, GoogleLoginProvider, FacebookLoginProvider} from 'angular4-social-login';
import { Client } from 'src/app/models/client';
import { Observable, throwError, Subscriber, Subject } from 'rxjs';

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  @Output() registerFlag = new EventEmitter<any>(); 
  // mysocialObj:Client;
  loginForm: FormGroup;
   email = new FormControl('',[
    Validators.required,
    Validators.minLength(3),
    Validators.maxLength(100),
    Validators.pattern(EMAIL_REGEX)
  ]);
  hash = new FormControl('',[
    Validators.required,
    Validators.minLength(6)
  ]);
  // phone = new FormControl('',[
  //   Validators.required,
  //   Validators.minLength(10),
  //   Validators.maxLength(10),
  // ])
  constructor(    private auth: AuthWedService,
    private formBuilder: FormBuilder,
    private router: Router,
    private _socioAuthServ:AuthService) { 
  }

  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      email: this.email,
      hash:this.hash,
    }); 
  }
  login() {
    
    this.auth.signIn(this.loginForm.value).subscribe((res: any) => {
      localStorage.setItem('access_token', res.token)
      // this.emit('client name added',res._id);
      console.log(res._id);
      this.auth.getClientProfile(res._id).subscribe((res) => {
        this.auth.current_Client = res;
        localStorage.setItem('_id', res._Id)
        localStorage.setItem('current_client',JSON.stringify(this.auth.current_Client));
        console.log(this.auth.current_Client);
        this.auth.showSwal('success-message','Welcome', 'you have been logged in successfully');
        // this.isloggedin = true;
        var obj = {type:'auth',flag:true};
        this.registerFlag.emit(obj);
        // location.assign('/');
      });
    },(err:any)=>{
      this.auth.showSwal('warning-message', 'Wrong', 'you must input correct info!!!')
      var obj = {type:'auth',flag:false};
      this.registerFlag.emit(obj);
      // this.isloggedin = false;
    })
    console.log('login');
  } 
  onRegister(){
    var obj = {type:'register',flag:false};
    this.registerFlag.emit(obj);
  }
  google(platform:string):void{
    console.log('google func');
    platform = GoogleLoginProvider.PROVIDER_ID;
    // this.auth.googleLogin();
    this._socioAuthServ.signIn(platform).then((response)=>{
        console.log('ddd'+ response);
        // this.mysocialObj.providerId = response.id;
        // this.mysocialObj.fullname = response.name;
        // this.mysocialObj.email = response.email;
        // this.mysocialObj.password = '1';
        console.log(response);
        var myObj = {
          providerId:response.id, fullname:response.name, email:response.email,
          authToken:response.authToken,logtype:1
        };
        this.auth.googleLogin(myObj).subscribe((res: any) => {
          console.log('googletoken' + res.token);
          localStorage.setItem('access_token', res.token)
          // this.emit('client name added',res._id);
          this.auth.getClientProfile('google').subscribe((res) => {
            console.log('googleclientprofile')
            this.auth.current_Client = res;
            localStorage.setItem('current_client',JSON.stringify(this.auth.current_Client));
            console.log(this.auth.current_Client);
            this.auth.showSwal('success-message','Welcome', 'you have been logged in successfully');
            // this.isloggedin = true;
            var obj = {type:'auth',flag:true};
            this.registerFlag.emit(obj);
            // location.assign('/');
          });
        },(err:any)=>{
          this.auth.showSwal('warning-message', 'Wrong', 'you must input correct info!!!')
          var obj = {type:'auth',flag:false};
          this.registerFlag.emit(obj);
          // this.isloggedin = false;
        })
        
      });
  }
  facebook(platform:string):void{
    console.log('facebook func');
    platform = FacebookLoginProvider.PROVIDER_ID;
    // this.auth.googleLogin();
    this._socioAuthServ.signIn(platform).then((response)=>{
        console.log('ddd'+ response);
        // this.mysocialObj.providerId = response.id;
        // this.mysocialObj.fullname = response.name;
        // this.mysocialObj.email = response.email;
        // this.mysocialObj.password = '1';
        console.log(response);
        var myObj = {
          providerId:response.id, fullname:response.name, email:response.email,
          authToken:response.authToken,logtype:1
        };
        this.auth.facebookLogin(myObj).subscribe((res: any) => {
          console.log('facebook' + res.token);
          localStorage.setItem('access_token', res.token)
          // this.emit('client name added',res._id);
          this.auth.getClientProfile('google').subscribe((res) => {
            console.log('googleclientprofile')
            this.auth.current_Client = res;
            localStorage.setItem('current_client',JSON.stringify(this.auth.current_Client));
            console.log(this.auth.current_Client);
            this.auth.showSwal('success-message','Welcome', 'you have been logged in successfully');
            // this.isloggedin = true;
            var obj = {type:'auth',flag:true};
            this.registerFlag.emit(obj);
            // location.assign('/');
          });
        },(err:any)=>{
          this.auth.showSwal('warning-message', 'Wrong', 'you must input correct info!!!')
          var obj = {type:'auth',flag:false};
          this.registerFlag.emit(obj);
          // this.isloggedin = false;
        })
        
      });
  }
}
