import { Component, OnInit } from '@angular/core';
import PerfectScrollbar from 'perfect-scrollbar';
import { AuthWedService } from '../services/auth.service';
import { Router } from '@angular/router';

declare const $: any;

//Metadata
export interface RouteInfo {
    path: string;
    title: string;
    type: string;
    icontype: string;
}

//Menu Items
export const ROUTES: RouteInfo[] = [  {
    path: 'vendors',
    title: 'Vendors',
    type: 'link',
    icontype: 'assignment_ind'
},{
    path: 'clients',
    title: 'Clients',
    type: 'link',
    icontype: 'account_box'
},{
    path: 'business',
    title: 'Business',
    type: 'link',
    icontype: 'business_center'
}
,{
    path: 'reviews',
    title: 'Review',
    type: 'link',
    icontype: 'comment'
},{
    path: 'blog',
    title: 'Blog',
    type: 'link',
    icontype: 'menu_book'
},{
    path: 'location',
    title: 'City & Locations',
    type: 'link',
    icontype: 'location_city'
}
];
@Component({
    selector: 'app-ownersidebar-cmp',
    templateUrl: 'ownersidebar.component.html',
})

export class OwnersidebarComponent implements OnInit {
    public menuItems: any[];
    ps: any;
    isMobileMenu() {
        if ($(window).width() > 991) {
            return false;
        }
        return true;
    };
    constructor(private auth:AuthWedService,private router:Router){
            this.menuItems = ROUTES.filter(menuItem => menuItem);
    }
    ngOnInit() {
        // if(this.auth.get)
            // localStorage.setItem('vendor_sidebar_flag','false')
            this.router.events.subscribe((res)=>{
                    this.menuItems = ROUTES.filter(menuItem => menuItem);
        })
     
        if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
            const elemSidebar = <HTMLElement>document.querySelector('.sidebar .sidebar-wrapper');
            this.ps = new PerfectScrollbar(elemSidebar);
        }
    }
    updatePS(): void  {
        if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
            this.ps.update();
        }
    }
    isMac(): boolean {
        let bool = false;
        if (navigator.platform.toUpperCase().indexOf('MAC') >= 0 || navigator.platform.toUpperCase().indexOf('IPAD') >= 0) {
            bool = true;
        }
        return bool;
    }
}
