import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'mysearch'
})
export class MysearchPipe implements PipeTransform {

  transform(pageOfItems: any[], filter: Object): any {
    if (!pageOfItems || !filter) {
        return pageOfItems;
    }
    // filter items array, items which match and return true will be
    // kept, false will be filtered out
    return pageOfItems.filter(item => {
      console.log('>>>><>>>>> PIPE <><><>')
      // console.log(item)
      console.log(filter)
      console.log('--------')
      if(item.roomsAvaliable != undefined && item.roomsAvaliable == filter['isRoomsAvaliable']) return true;
      if(item.travelOtherIndianCities != undefined && item.travelOtherIndianCities == filter['isTravelOtherIndianCities']) return true;
      if(item.haveStudio != undefined && item.haveStudio == filter['isHaveStudio']) return true;
      if(item.travelToClientVenue != undefined && item.travelToClientVenue == filter['isTravelToClientVenue']) return true;
      if(item.provideBackupDancers != undefined && item.provideBackupDancers == filter['isProvideBackupDancers']) return true;
      console.log('>>>><>>>>> ENDPIPE <><><>')
      return false;
    });
}

}
