import { Injectable } from "@angular/core";
import { HttpInterceptor, HttpRequest, HttpHandler } from "@angular/common/http";
import { AuthWedService } from "./auth.service";

@Injectable()

export class AuthInterceptor implements HttpInterceptor {
    constructor(private authService: AuthWedService) {       
}

    intercept(req: HttpRequest<any>, next: HttpHandler) {
        const authToken = this.authService.getToken();
        const authTokenVendor = this.authService.getTokenVendor();
        const authTokenOwner= this.authService.getTokenOwner();
        if(authToken){
        req = req.clone({
            setHeaders: {
                Authorization:  "" + authToken
            }
        });
        }else if(authTokenVendor){
            req = req.clone({
                setHeaders: {
                    Authorization:  "" + authTokenVendor
                }
            }); 
        }else{
            req = req.clone({
                setHeaders: {
                    Authorization:  "" + authTokenOwner
                }
            });
        }


        return next.handle(req);
    }
}